import React, { Component } from "react";
import { Button, Icon } from "antd";
import Frameani from "frameani";
class Animation extends Component {
  arr = Array(100)
    .fill(0)
    .map(i => {
      return {
        left: Math.random() * 100,
        top: Math.random() * 100
      };
    });

  componentDidMount() {
    let frameani;
    const balls = Array.from(document.querySelectorAll(".ball"));
    // 
    frameani = new Frameani({
      value: [50, 10],
      timingFunction: "easeInOutSine",
      render: value => {
        balls.forEach(ball => {
          ball.style.top = value + "%";
          ball.style.left = value + "%";
        });
      }
    });

    // 
    balls.forEach((ball, index) => {
      if (index === 0) {
        frameani = frameani.to(this.getAniDistribute(ball, this.arr[index]));
      } else {
        frameani.with(this.getAniDistribute(ball, this.arr[index]));
      }
    });

    // 
    balls.forEach((ball, index) => {
      if (index === 0) {
        frameani = frameani.to(this.getAniPolymeric(ball, this.arr[index]));
      } else {
        frameani = frameani.with(this.getAniPolymeric(ball, this.arr[index]));
      }
    });

    // 
    frameani.to({
      value: [10, 50],
      timingFunction: "easeInOutSine",
      render: value => {
        balls.forEach(ball => {
          ball.style.top = value + "%";
          ball.style.left = value + "%";
        });
      }
    });

    this.frameani = frameani;
  }

  getAniPolymeric(ball, pos) {
    return {
      value: [[pos.left, 10], [pos.top, 10]],
      duration: 3000,
      timingFunction: "easeInOutElastic",
      render: (x, y) => {
        ball.style.left = x + "%";
        ball.style.top = y + "%";
      }
    };
  }

  getAniDistribute(ball, pos) {
    return {
      value: [[10, pos.left], [10, pos.top]],
      duration: 3000,
      timingFunction: "easeInOutElastic",
      render: (x, y) => {
        ball.style.left = x + "%";
        ball.style.top = y + "%";
      }
    };
  }

  handleStart = () => {
    console.log("");
    this.frameani.play();
  };

  handleEnd = () => {
    this.frameani.end();
  };

  handleStop = () => {
    this.frameani.stop();
  };

  handleReset = () => {
    this.frameani.reset();
  };

  render() {
    const btnBoxStyle = {
      position: "absolute",
      top: 0,
      right: 0,
      margin: 20
    };

    const aBtnStyle = {
      position: "absolute",
      bottom: 0,
      left: 0,
      margin: 20,
      zIndex: 999
    };

    return (
      <div style={{ width: "100%", height: "100%", position: "relative" }}>
        <Button
          type="primary"
          href="https://github.com/JS-Hao/frameani/blob/master/docs/README.zh.md"
          style={aBtnStyle}
        >
          <Icon type="github" theme="outlined" />
          go to github
        </Button>
        <div
          id="balls"
          style={{
            position: "relative",
            width: "100%",
            height: "100%",
            overflow: "hidden "
          }}
        >
          {this.arr.map((e, i) => {
            return (
              <div
                className="ball"
                key={i}
                style={{
                  width: 50,
                  height: 50,
                  borderRadius: 25,
                  position: "absolute",
                  left: "50%",
                  top: "50%",
                  transform: "translate(-50%, -50%)",
                  backgroundColor:
                    "#" +
                    (
                      "00000" + ((Math.random() * 0x1000000) << 0).toString(16)
                    ).slice(-6)
                }}
              />
            );
          })}
        </div>
        <div id="btn-box" style={btnBoxStyle}>
          <Button onClick={this.handleStart}>START</Button>
          <Button onClick={this.handleEnd}>END</Button>
          <Button onClick={this.handleStop}>STOP</Button>
          <Button onClick={this.handleReset}>RESET</Button>
        </div>
      </div>
    );
  }
}

export default Animation;
