import React, { Component } from "react";
import { Button } from "antd";
import Core from "../lib/sketchpad.min";
class Sketchpad extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tool: "pen"
    };
  }

  isInit = false;

  componentDidMount() {
    setTimeout(this.init, 0);
  }

  init = () => {
    if (this.isInit) {
      return;
    }
    this.isInit = true;
    this.core = new Core(this.refs.wrapper);
    this.core.init(this.getParam());
  };

  handleEraser = () => {
    this.setState({ tool: "eraser" });
    this.core.setTool("eraser");
  };

  handleDrag = () => {
    this.setState({ tool: "drag" });
    this.core.setTool("drag");
  };

  handlePen = () => {
    this.setState({ tool: "pen" });
    this.core.setTool("pen");
  };

  handleUndo = () => {
    this.setState({ tool: "undo" });
    this.core.undo();
  };

  handleClear = () => {
    this.setState({ tool: "clear" });
    this.core.clear();
  };

  getStyle(tool) {
    if (this.state.tool === tool) {
      return {
        background: "#1890ff",
        color: "#fff"
      };
    } else {
      return {};
    }
    // return `sketchpad-btn ${this.state.tool === tool ? "active" : ""}`;
  }

  getParam() {
    return {
      pen: {
        size: 2
      },
      eraser: {
        ico: require("../iamges/eraser.png"),
        w: 38,
        h: 43,
        offsetX: 19,
        offsetY: 21.5,
        type: "rectangle"
      },
      maxStack: 5
    };
  }

  render() {
    const bgStyle = {
      position: "absolute",
      left: 0,
      bottom: 0
    };
    const wrapperStyle = {
      boxShadow: "2px 2px 20px #ccc",
      border: "1px solid #eee",
      background: "#F0F8FF",
      height: "100%"
    };
    return (
      <div
        style={{
          height: "100%",
          position: "relative"
        }}
      >
        <div className="sketchpad-wrapper" ref="wrapper" style={wrapperStyle} />
        <Button.Group style={bgStyle}>
          <Button style={this.getStyle("pen")} onClick={this.handlePen}>
            
          </Button>
          <Button style={this.getStyle("eraser")} onClick={this.handleEraser}>
            
          </Button>
          <Button style={this.getStyle("drag")} onClick={this.handleDrag}>
            
          </Button>
          <Button style={this.getStyle("undo")} onClick={this.handleUndo}>
            
          </Button>
          <Button style={this.getStyle("clear")} onClick={this.handleClear}>
            
          </Button>
        </Button.Group>
      </div>
    );
  }
}

export default Sketchpad;
